.card {
    background: lemonchiffon;
    border-radius: 3px;
    height: 170px;
    margin: 10px;
    border: 5px solid #f1f1f1;
    display: inline-block;
    width: 170px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    -webkit-transform: 2s;
            transform: 2s;
}

.card:hover {
   
    cursor: pointer;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.card > .img-container {
    height: 100%;
    overflow: hidden;
    border-radius: 3px;
    text-align: center;
}

.card > .img-container > img {
    margin-top: 15%;
    width: 60%;
}

.card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
}

.card > .content > ul {
    list-style-type: none;
}

.card > .content > ul > li {
    margin-top: 18px;
}

.cardWrapper {
    padding-top: 120px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto;
    flex-wrap: wrap;
    justify-content: center;
}

.wrapper {
  background: #ECEFF1;
  min-height: 80%;
  height: 100%;
  display: block;
  overflow: auto;
}
.nav-bar{
  width: 100%;
  position: fixed;
  background-color:#5133A4; 
  box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
  z-index: 99;
  white-space: nowrap;
}

footer{
  width: 100%;
  font-weight: bold;
  color: #FFF;
  background-color: #5137A1;
  height: 30px;
  bottom: 0;
position: fixed;
}

footer p{
margin: 1%;

}
.title {
  width: 33%;
  text-align: center;
  margin-top: 0%;
  font-size: calc(10px + 14 * (100vw - 320px)/ 800);
  font-weight: 100;
  float: left;
  padding: 3%;
  color: #FFF;
}

.title:first-child{
  font-weight: bold;
}

.header{
  width: 100%;
  background-color: #2c323c;
  background-image: url("");
  float: left;
  height: 496px;
}
.header h1{
  padding-top: 18%;
  font-size: 3rem;
  color: #FFF;
  margin: 0%;
  text-align: center;
}
.header p{
  padding-top: 0%;
  margin-top: 0%;
text-align: center;
color: #FFF;
font-size: 1.5em;
font-weight: bold;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

